export default function HasfbCtrl() {
  var vm = this;
  vm.list = [
    {
      name: '',
      key: [
        {
          zikey: 'frontpic',
        },
        {
          zikey: 'title',
        }
      ],
      width: '30%',
      img: true
    },
    { name: '', key: 'projectType', width: '15%' },
    { name: '', key: 'Status', width: '15%' },
    {
      name: '', 
      key:
      [
        { zid: ' :',
          zikey: 'money'
        },
        { zid: '',
          zikey: 'progress',
          zikeyfh: '%'
        },
      ],
      width: '15%',
    },
    { name: '', key: 'moneySum', width: '15%' },
  ];
}



