export default function TzlistCtrl() {
  var vm = this;
  vm.list = [
    { name: '', key: 'title', width: '30%' },
    { name: '', key: 'name', width: '15%' },
    { name: '', key: 'status', width: '15%' },
    { name: '', key: [
      {
        zid: ' :',
        zikey: 'moneys'
      },
      {
        zid: ' :',
        zikey: 'progress',
        zikeyfh: '%'
      }
    ], width: '20%' },
    { name: '', key: 'supportmoney', width: '10%' },
    { name: '', key: 'supportnum', width: '10%' },
  ];
}



