export default function dpjCtrl() {
  var vm = this;
  vm.list = [
    { name: '', key: 'frontpic', width: '15%', img: true },
    { name: '', key: 'projectName', width: '20%' },
    { name: '', key: 'hbcontent', width: '20%' },
    { name: '', key: 'hbcount', width: '10%' },
    { name: '', key: 'status', width: '15%' },
    { name: '', key: 'cost', width: '10%' },
  ];
}



