export default function SellerCtrl($stateParams) {
  var vm = this;
  vm.listTabs = [
    {
      name: '',
      url: '.all',
    },
    {
      name: '',
      url: '.dfh',
    },

    {
      name: '',
      url: '.dsh',
    },
    {
      name: '',
      url: '.ysh',
    },
  ]
  vm.id = $stateParams.id;
}


